export default {
	data() {
		return {
			current: 0,
			rankList: [],
			tabList: [{
				name: '钻石争夺战'
			}, {
				name: '鲜花争夺战'
			}],
			pkStatus: 1,
			timeSpace: {},
			list: [],
			pkTime: '',
			mid: 0,
			yestoday: 0,
			adm: 0,
			captain: this.$app.getData('userStar').captain,
		};
	},
	onLoad(options) {
		console.log(options)
		let starname = options.starname || ''
		uni.setNavigationBarTitle({
			title: starname + '团战',
		})

		this.pkTime = options.pkTime || ''
		this.mid = options.mid
		this.yestoday = options.yestoday || 0
		if (options.current && options.current != 0) {
			this.current = options.current
			return
		}
		this.page = 1;
		this.loadData()
	},
	
	methods: {
		lower() {
			this.page++;
			if (this.page > 10) {
				return
			}
			this.loadData()
		},
		out(item,index) {
			const list = [2, 4, 8, 12, 24, 72];
			const itemList = []
			for (let v of list) {
				itemList.push(`禁止 ${item.name} 参加团战：${v}小时`)
			}
			wx.showActionSheet({
				itemList,
				success: res => {
					let time = list[res.tapIndex]
					wx.showModal({
						title: '提示',
						content: `禁止 ${item.name} ${time}小时参加团战?`,
						success: res => {
							if (res.confirm) {
								let uid = item.uid

								this.$app.request('rank/pk_out', {
									mid: this.$app.getData('userStar')['id'],
									uid: uid,
									time: time,
								}, res => {
									this.$app.toast(res.msg)
								})
							}
						}
					})
				}
			})

		},
		goPage(e) {
			if (this.pkStatus == 2) {
				if (e.currentTarget.dataset.mid == this.$app.getData('userStar').id || this.sAdm == 1) {
					this.$app.goPage(e.currentTarget.dataset.page)
				}
			}
		},
		swiperChange(e) {
			this.current = e.detail.current
			this.page = 1;
			this.rankList = []
			this.loadData()
		},
		loadData() {
			this.$app.request('rank/pk', {
				page: this.page,
				type: this.current || 0,
				mid: this.$app.getData('userStar')['id'],
				pkTime: this.pkTime || '',
				yestoday: this.yestoday || 0,
				s: 1,
			}, res => {

				let data = res.data

				if (this.pkTime) {
					data.status = 2
				}
				if (this.page == 1) {
					this.list = data.userList

				} else {
					this.list = this.list.concat(data.userList)
				}

				this.pkStatus = data.status
				this.isJoin = data.isJoin || 0
				this.adm = data.isAdm || 0


			})


		}
	}

}
